<template>
	<div class="sub-container question">
		<div class="page-title center">
			<h2>1:1 문의</h2>
		</div>
		<div class="question-container">
			<div class="question-header">
				<h5>
					{{ createDate && createDate.substr(0, 10) }}
					{{ showEnumAsString(categoryId) }} 관련 문의
				</h5>
				<h3>{{ title }}</h3>
			</div>
			<div class="question-body">
				<div class="content marB-20" v-html="contents"></div>
				<img src="https://preview.ibb.co/gQbzQ5/colt_steele_firemarshall.jpg" />
				<img :src="imgPath2" />
				<img :src="imgPath3" />
			</div>
			<ul
				class="answer-body"
				v-for="(commentItem, index) in commentList"
				:key="index"
			>
				<li>
					<p class="body2">
						{{ commentItem.contents }}
					</p>
					<p class="body2">
						관리자{{ commentItem.createId }}
						{{ commentItem && commentItem.createDate.substr(0, 10) }}
					</p>
				</li>
			</ul>
			<div class="button-container">
				<router-link
					:to="{
						name: 'EditUserQuestion',
						params: { id: detailId },
					}"
				>
					<button class="btn write-btn large" type="button">
						수정
					</button>
				</router-link>
				<button class="btn write-btn" @click="deleteQuestion(detailId)">
					삭제
				</button>
			</div>
		</div>
	</div>
</template>

<script>
import { deleteQuestion, fetchQuestionDetail } from '@/api/question';
import { enumToString } from '@/utils/enumToString';

export default {
	created() {
		this.detailId = this.$route.params.id;
		this.fetchQuestionDetail(this.detailId);
	},
	data() {
		return {
			detailId: '',
			categoryId: '',
			commentList: [],
			title: '',
			contents: '',
			createDate: '',
			imgPath1: '',
			imgPath2: '',
			imgPath3: '',
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchQuestionDetail(id) {
			const { data } = await fetchQuestionDetail(id);
			console.log('data', data);
			this.categoryId = data.categoryId;
			this.commentList = data.commentList;
			this.title = data.title;
			this.contents = data.contents;
			this.createDate = data.createDate;
			this.imgPath1 = data.imgPath1;
			this.imgPath2 = data.imgPath2;
			this.imgPath3 = data.imgPath3;
		},
		async deleteQuestion(id) {
			try {
				await deleteQuestion(id);
			} catch (e) {
				console.log(e);
			} finally {
				history.back();
			}
		},
		showEnumAsString(enumId) {
			return enumToString(enumId);
		},
	},
};
</script>

<style lang="scss" scoped>
.question-container {
	width: 700px;
	margin: 0 auto;
}
.question-header {
	padding-bottom: 24px;
	border-bottom: 2px solid #e6e6e6;
}
.question-header h5 {
	color: #8c8c8c;
	margin-bottom: 8px;
}
.question-body {
	padding: 24px 0;
	border-bottom: 2px solid #e6e6e6;
}
.question-body img {
	width: 100%;
	max-width: 700px;
}
.answer-body {
	padding: 24px 0;
	border-bottom: 2px solid #00ad84;
}
.button-container {
	padding-top: 30px;
	display: flex;
	justify-content: flex-end;
}
.button-container button:nth-child(2) {
	margin-left: 10px;
}
</style>
